.order-popup {
  &__overlay {
    position: fixed;
    height: 100vh;
    width: 100%;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 99999;
    opacity: 0;
    visibility: hidden;

    &.active {
      opacity: 1;
      visibility: visible;
    }
  }

  &__dialog {
    min-height: 300px;
    width: 96%;
    max-width: 500px;
    padding: 50px;
    background-color: #fafafa;
    overflow-y: auto;
    position: relative;

    @media (max-height: 630px) {
      height: 96vh;
    }

    @include mobile {
      padding: 50px 25px;
    }
  }

  &-form {
    margin-top: 35px;

    input {
      padding: 16px 0;

      @include tablet {
        padding: 13px 0;
        font-size: 14px;
        line-height: 16px;
      }

      @include mobile {
        padding: 11px 0;
      }
    }

    textarea {
      padding: 16px 0;
      height: 100px;
      margin-top: 20px;

      &::placeholder {
        color: $black;
      }

      @include tablet {
        padding: 13px 0;
        font-size: 14px;
        line-height: 16px;
      }

      @include mobile {
        padding: 11px 0;
      }
    }

    &__label {
      &::before {
        top: 10px;
      }

      a {
        color: $black;
        text-decoration: underline;
        transition: 0.3s linear;
        // &:hover {
        //   color: $darkGrey;
        // }

        @include mobile {
          display: block;
        }
      }
    }

    &__checkbox {
      &-wrap {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        margin-top: 25px;
      }
    }

    &__name {
      color: #5f5f5f;
      border-bottom: 1px solid green;

      &.iscomplete {
        border-bottom: 1px solid #e5e5e5;
      }
    }

    &__email {
      margin-top: 20px;
      color: #5f5f5f;
      border-bottom: 1px solid green;

      &.iscomplete {
        border-bottom: 1px solid #e5e5e5;
      }
    }

    &__phone {
      margin-top: 20px;
      color: #5f5f5f;
      border-bottom: 1px solid green;

      &.iscomplete {
        border-bottom: 1px solid #e5e5e5;
      }
    }
    &__msg {
      margin-top: 20px;
      color: #5f5f5f;
      border-bottom: 1px solid green;

      &.iscomplete {
        border-bottom: 1px solid #e5e5e5;
      }
    }

    &__btn {
      margin-top: 30px;
    }
  }

  &__close {
    width: 40px;
    height: 40px;
    cursor: pointer;
    border: none;
    transition: 0.4s;
    position: absolute;
    right: 15px;
    top: 15px;
    @include mobile {
      width: 30px;
      height: 30px;
    }

    svg {
      path {
        transition: 0.3s linear;
      }
    }

    &:hover {
      background-color: $black;

      svg {
        path {
          fill: #fff;
        }
      }
    }

    img {
      @include mobile {
        width: 30px;
        height: 30px;
      }
    }
  }

  &__title {
    font-size: 36px;
    line-height: 42px;
    @include mobile {
      font-size: 18px;
      line-height: 22px;
    }
  }

  input[type="checkbox"]:checked + label:after {
    top: 12px;
  }
}

.order {
  &__popup {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    z-index: 50;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.3s;
    opacity: 0;
    visibility: hidden;

    &--success {
      background-color: rgba(55, 165, 140, 0.7);

      &.active {
        opacity: 1;
        visibility: visible;
      }
    }

    &--error {
      background-color: rgba(215, 68, 62, 0.7);

      &.active {
        opacity: 1;
        visibility: visible;
      }
    }

    &-content {
      background-color: #fff;
      padding: 35px;
      display: flex;
      flex-direction: column;

      @include tablet {
        padding: 25px;
      }

      @include mobile {
        padding: 20px;
      }
    }

    &-image {
      width: 40px;
      align-self: center;

      @include tablet {
        width: 35px;
      }

      @include mobile {
        width: 30px;
      }
    }

    &-title {
      font-size: 30px;
      line-height: 36px;
      font-weight: bold;
      margin-top: 20px;

      @include tablet {
        font-size: 24px;
        line-height: 29px;
      }
      @include mobile {
        font-size: 20px;
        line-height: 24px;
      }
    }
  }
}
